import $lyfw from '@/common/LYFW/LyfwFmq.js' //旅游服务
export default {
	data() {
		return {
			countDownDate: 300, //倒计时时间
			hiddenValues: '0', //隐藏状态值
			channel: [{
				name: '微信'
			}, {
				name: '支付宝'
			}],
			channeIndex: 0, //选择支付方式
			paymentValue: false, //默认未拉起状态

			cancelStatus: false, //取消状态

			orderInfo: [{
				orderNumber: '',
				orderType: '',
				orderActualPayment: '',
				orderDateReminder: '',
				orderDate: '',
				orderCountdown: '',
				orderInsure: '',
				orderInsurePrice: '',

				ticketId: '',
				ticketName: '',
				ticketOpenUp: '',
				ticketTitle: '',
				ticketContain: '',
				ticketAdultPrice: '',
				ticketChildPrice: '',

				couponID: '',
				couponTitle: '',
				couponPrice: '',
				couponCondition: '',

				addressData: [{
					passengerId: '',
					userType: '',
					userName: '',
					userSex: '',
					userCodeNum: '',
					userPhoneNum: '',
					userDefault: '',
					userEmergencyContact: '',
				}]

			}],


			adultIndex: '', //成人数量
			childrenIndex: '', //儿童数量	
			adultTotalPrice: '', //成人总价
			childrenTotalPrice: '', //儿童总价

			submitH5Data: '', //公众号H5支付参数

		}
	},
	onLoad: function(options) {
		uni.showLoading({
			title: '拉起订单中...'
		})

		uni.request({
			url:$lyfw.Interface.spt_RequestTicketsListDetail.value,
			method:$lyfw.Interface.spt_RequestTicketsListDetail.method,
			data: {
				orderNumber: options.orderNumber
			},
			header: {
				'content-type': 'application/json'
			},
			success: (res) => {
				// console.log(res)
				this.orderInfo = res.data.data[0];
				// console.log(this.orderInfo)
				this.screenUser();
				this.getDate();
				uni.hideLoading()
			}
		})










	},
	onShow:function(){
		var that = this;
		uni.request({
			url:$lyfw.Interface.spt_getIsPaySuccess.value,
			method:$lyfw.Interface.spt_getIsPaySuccess.method,
			data: {
				orderNumber: that.orderInfo.orderNumber,
			},
			header: {'content-type': 'application/json'},
			success:function(res){
				console.log(res)
					if(res.data.msg =='当前订单已支付成功,请勿重复支付'){
						uni.request({
							url:$lyfw.Interface.spt_RequestTickets.value,
							method:$lyfw.Interface.spt_RequestTickets.method,
							data: {
								orderNumber: that.orderInfo.orderNumber
							},
							header: {
								'content-type': 'application/json'
							},
							success: function(res) {
								// console.log(res)
								if (res.data.msg == '出票成功') {
									uni.redirectTo({ 
										url: '/pages/LYFW/scenicSpotTickets/successfulPayment'
									})
								} else if (res.data.msg == '出票失败') {
									uni.showToast({
										title: '出票失败，联系客服出示订单编号',
										icon: 'none',
										duration: 3000
									})
								}else if (res.data.msg == '您当前无可出票的订单或该订单已出票') {
									uni.redirectTo({
										url: '/pages/LYFW/scenicSpotTickets/successfulPayment'
									})
								}
							},
							fail: function() {
								uni.showToast({
									title: '服务器异常，请联系客服',
									icon: 'none',
									duration: 3000
								})
							}
						})
					}
			},
			fail:function(){
				uni.showToast({
					title:'网络异常，请检查网络',
					icon:'none'
				})
			}
		})
	},
	methods: {
		//隐藏操作
		hide(e) {
			if (e == 0) {
				this.hiddenValues = 1;
			} else {
				this.hiddenValues = 0;
			}
		},

		//同意购买-点击事件
		Selection: function() {
			if (this.channeIndex == 0) {
				this.channeIndex = 1;
			} else {
				this.channeIndex = 0;
			}
		},

		//数组提取
		screenUser: function() {
			let adult = this.orderInfo.addressData.filter(item => {
				return item.userType == '成人';
			})
			let children = this.orderInfo.addressData.filter(item => {
				return item.userType == '儿童';
			})

			this.adultIndex = adult.length;
			this.childrenIndex = children.length;
			this.adultTotalPrice = adult.length * this.orderInfo.ticketAdultPrice;
			this.childrenTotalPrice = children.length * this.orderInfo.ticketChildPrice;
		},


		//获取当前时间并格式化
		getDate: function() {
			//先提取订单下单时间把空格转换成T
			var a = this.orderInfo.setOrderTime.replace(' ', 'T')
			//把时间转换成时间戳
			var b = new Date(a).getTime();
			// console.log(a)
			// console.log(b)
			//获取当前时间（为什么要先把当前时间戳格式化？）是因为直接获取当前时间戳存在时间误差
			var date = new Date(),
				year = date.getFullYear(),
				month = date.getMonth() + 1,
				day = date.getDate(),
				hour = date.getHours() < 10 ? "0" + date.getHours() : date.getHours(),
				minute = date.getMinutes() < 10 ? "0" + date.getMinutes() : date.getMinutes(),
				second = date.getSeconds() < 10 ? "0" + date.getSeconds() : date.getSeconds();
			month >= 1 && month <= 9 ? (month = "0" + month) : "";
			day >= 0 && day <= 9 ? (day = "0" + day) : "";
			var timer = year + '-' + month + '-' + day + 'T' + hour + ':' + minute + ':' + second;
			//把转换后的时间，转换成时间戳
			// console.log(timer)
			var c = new Date(timer).getTime();

			// console.log(c)

			//用当前时间-下单时间再除于1000就是秒
			var d = (c - b) / 1000;
			// console.log(d)
			//这里的300秒就是支付倒计时，门票是5分钟
			var e = 307 - d;
			// 46

			this.countDownDate = e;


			if (e <= 0) {
				uni.showToast({
					title: '订单已超时',
					icon: 'none',
					success: ()=>{
						this.countDown()
						uni.switchTab({
							url:'../../order/OrderList'
						})
					}
				})

			} else {
				this.countDown();
			}
		},


		//支付倒计时
		countDown: function() {
			uni.hideLoading()
			var interval = setInterval(() => {
				--this.countDownDate;
				uni.setStorage({
					key: 'countDown',
					data: this.countDownDate,
				})
				if (this.countDownDate <= 0) {
					clearInterval(interval)
					this.countDownEnd();
					uni.removeStorage({
						key: 'countDown'
					})
				}
			}, 1000)
		},

		//倒计时结束
		countDownEnd: function() {
			uni.request({
				url:$lyfw.Interface.spt_RequestTicketsListDetail.value,
				method:$lyfw.Interface.spt_RequestTicketsListDetail.method,
				data: {
					orderNumber: this.orderInfo.orderNumber
				},
				header: {
					'content-type': 'application/json'
				},
				success: (res) => {
					console.log(res)
					if (res.data.data[0].orderType == '待支付') {
						uni.request({
							url:$lyfw.Interface.spt_CancelTickets.value,
							method:$lyfw.Interface.spt_CancelTickets.method,
							data: {
								orderNumber: res.data.data[0].orderNumber
							},
							header: {
								'content-type': 'application/json'
							},
							success(res) {
								console.log(res)
								uni.showToast({
									title: '支付超时，已自动取消订单,请刷新订单',
									icon: 'none',
									duration: 2000,
									success: () => {
										uni.switchTab({
											url: '../../order/OrderList'
										})
									}
								})

							},
							fail() {
								uni.showToast({
									title: '自动取消失败，请检查网络状态',
									icon: 'none',
									duration: 2000,
									success: function() {
										uni.switchTab({
											url: '../../order/OrderList'
										})
									}
								})
							}
						})
					} else {
						return false
					}
				}
			})

		},

		//禁止重复提交操作
		paymentSatas: function() {
			if (this.paymentValue == false) {
				this.paymentValue == true
				this.payment()
			} else if (this.paymentValue == true) {
				uni.showToast({
					title: '请勿重复点击支付'
				})
			}
		},

		//调起支付
		payment: function() {
			var that = this;
			uni.showLoading({
				title: '拉起支付中...'
			})
			// 查看服务商
			uni.getProvider({
				service: 'payment',
				success: function(res) {
					console.log(res)
				}
			})


			//--------------------------------------微信小程序支付-------------------------------------
			if (that.channeIndex == 0) {
				console.log(this.orderInfo.orderNumber)
				uni.request({
					url:$lyfw.Interface.spt_Pay.value,
					method:$lyfw.Interface.spt_Pay.method,
					data: {
						payType: 5,
						price: that.orderInfo.orderActualPayment,
						orderNumber: that.orderInfo.orderNumber,
						goodsName: that.orderInfo.title,
						billDescript: that.orderInfo.ticketTitle
					},
					header: {'content-type': 'application/json'},
					success: function(res) {
						console.log(res)
						uni.requestPayment({
							provider: 'wxpay',
							timeStamp: res.data.data.Timestamp,
							nonceStr: res.data.data.Noncestr,
							package: res.data.data.Noncestr,
							signType: 'MD5',
							paySign: res.data.data.Sign,
							success: function() {
								if (res.err_msg == "request:ok") {
									//支付成功再进计时器查询状态
									// location.href = "/Order/BaseCallback/" + flowID;
									// alert("支付成功");
									uni.request({
										url:$lyfw.Interface.spt_RequestTickets.value,
										method:$lyfw.Interface.spt_RequestTickets.method,
										data: {
											orderNumber: that.orderInfo.orderNumber
										},
										success: function(res) {
											if (res.data.msg == '出票成功') {
												uni.redirectTo({
													url: '/pages/LYFW/scenicSpotTickets/successfulPayment'
												})
											} else {
												uni.showToast({
													title: '出票失败，联系客服出示订单编号',
													icon: 'none',
													duration: 3000
												})
											}
										},
										fail: function() {
											uni.showToast({
												title: '出票失败，请联系客服出示订单编号',
												icon: 'none',
												duration: 3000
											})
										}
									})

								} else if (res.err_msg == "get_brand_wcpay_request:cancel") {
									// alert("您取消了支付，请重新支付");
									uni.showToast({
										title: '您取消了支付，请重新支付',
										icon: 'none'
									})
								} else if (res.err_msg == "get_brand_wcpay_request:faile") {
									// alert("支付失败，请重新支付"); 
									uni.showToast({
										title: '支付失败，请重新支付',
										icon: 'none',
										success: function() {
											uni.switchTab({
												url: '../../order/OrderList'
											})
										}
									})

								} else {
									// location.href = "/Coach/GetCoach";
								}

							},
							fail: function() {
								uni.showToast({
									title: '拉起支付失败，请查看网络状态'
								})
							}

						})
					},
					fail: function() {
						uni.showToast({
							title: '请求支付参数失败，请查看网络状态'
						})
					}
				})
			} else {
				uni.showToast({
					title: '请选择微信支付',
					icon: 'none'
				})
			}

































































































































































































































































































		}


	}
}
